.Coin{
    display:block;
    height: 100px;
    width: 100px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2em;
    margin-top: 1.5em;
    border: 3px solid rgba(17, 17, 17, 0.349);
    border-radius: 50%;
    padding: 1rem;
    background-color: #FFF;;
 }

 .Coin img{
    height:inherit;
    width: inherit;
    
 }
 .animate-coin {
    animation: flip 500ms 1;
  }
  
  @keyframes flip {
    0% {
      transform: rotateY(0deg);
      
    }
    50% {
      transform: rotateY(3600deg);
      
    }
    100% {
      transform: rotateY(7200deg);
      
    }
  }
